<template>
    <div class="bg-white">
        <div class="animate__animated animate__fadeInRight" v-if="ready">
            <div id="countdown"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <countdown :particle='true'/>
            </div>
            <div id="mempelai"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <mempelai  :particle='true'/>
            </div>
            <div id="acara"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <acara  :particle='true'/>
            </div>
            <div id="peta"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <peta  :particle='true'/>
            </div>
            <div id="galery"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <galery  :particle='true'/>
            </div>
            <div id="video"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <videos  :particle='true'/>
            </div>
            <div id="comment" >
                <comment  :particle='true'/>
            </div>
             <div id="doa"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <doa  :particle='true'/>
            </div>
             <div id="penutup"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <penutup  :particle='true'/>
            </div>
             <div id="credit"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <credit  />
            </div>
            <div id="musik"  :style="`color:${$store.state.data.undangan.text1_color}`">
                <musik />
            </div>
             
        </div>
        <div v-else>
            <p class="animated bounce infinite text-center">
                Loading
            </p>
        </div>
        <br>
        <br>
        <br>
        <br>
        <navigation1 />
    </div>
</template>
<script>
import autophp from '@/plugins/autophp';
let sdb = new autophp();
let urlnya="https://undangan.officialapp.website";
// ganti nikah , aqiqah
import countdown from '@/components/app_undangan/ultah/countdown.vue'
import mempelai from '@/components/app_undangan/ultah/mempelai.vue'
import acara from '@/components/app_undangan/ultah/acara.vue'
import peta from '@/components/app_undangan/ultah/peta.vue'
import galery from '@/components/app_undangan/ultah/galery.vue'
import videos from '@/components/app_undangan/ultah/video.vue'
import comment from '@/components/app_undangan/ultah/comment.vue'
import doa from '@/components/app_undangan/ultah/doa.vue'
import penutup from '@/components/app_undangan/ultah/penutup.vue'
import credit from '@/components/app_undangan/ultah/credit.vue'
import musik from '@/components/app_undangan/ultah/musik.vue'
import navigation1 from '@/components/app_undangan/ultah/navigation.vue'
export default{
    data(){
        return{
            undangan:[],
            ready:false
        }
    },
     components: {
        countdown,
        mempelai,
        acara,
        peta,
        galery,
        videos,
        comment,
        doa,
        penutup,
        credit,
        musik,
        navigation1,
    },
    mounted() {
        this.ready=true;
        // this.getUndangan();
        // this.getGalery();
        // this.getAcara();
    },
    methods: {
        getUndangan(){
        let that=this;
        sdb.collection("app_undangan_nikah",false,urlnya).doc().select(`select *,tb1.id as id from app_undangan_nikah tb1 left join app_undangan_nikah_mempelai tb2 on tb2.id=tb1.mempelai where tb1.id='${this.$route.query.id}'`).then(res => {
                that.$store.state.data.undangan=res[0]
                 sdb.collection("app_undangan_nikah_galery",false,urlnya).doc().select(`select * from app_undangan_nikah_galery where undangan='${that.$route.query.id}'`).then(res => {
                    that.$store.state.data.galery=res
                     sdb.collection("app_undangan_nikah_acara",false,urlnya).doc().select(`select * from app_undangan_nikah_acara where undangan='${that.$route.query.id}' order by id desc`).then(res => {
                        that.$store.state.data.acara=res
                        that.ready=true;
                        that.$forceUpdate();
                    });
                });
            });
        },
        getGalery(){
            // sdb.collection("app_undangan_nikah_galery",false,urlnya).doc().select(`select * from app_undangan_nikah_galery where undangan='${that.$route.query.id}'`).then(res => {
            //     this.$store.state.data.galery=res
            //     this.$forceUpdate();
            // });
        },
        getAcara(){
            sdb.collection("app_undangan_nikah_acara",false,urlnya).doc().select(`select * from app_undangan_nikah_acara where undangan='${this.$route.query.id}'`).then(res => {
                this.$store.state.data.acara=res
                this.$forceUpdate();
            });
        },
    },
}
</script>
