<script>
import 'particles.js'
import CurveText from '@inotom/vue-curve-text';
import autophp from '@/plugins/autophp';
 var id = require("date-fns/locale/id");
let sdb = new autophp();
let urlnya="https://undangan.officialapp.website";
export default{
    data(){
        return{
            undangan:[],
            tanggal:''
        }
    },
    components: {
        CurveText,
    },
  props:['particle'],
    mounted() {
        window.particlesJS("particlesjs", this.$store.state.particleImage)
        this.tanggal = this.timeDiffCalc(new Date(this.$store.state.data.acara[0].tanggal_acara),new Date());
        console.log(this.tanggal)
    },
    methods: {
        getUndangan(){
            sdb.collection("app_undangan_nikah",false,urlnya).doc().select(`select *,tb1.id as id from app_undangan_nikah tb1 left join app_undangan_nikah_mempelai tb2 on tb2.id=tb1.mempelai where tb1.id='${this.$route.query.id}'`).then(res => {
                this.undangan=res[0];
                console.log(this.undangan)
                this.tanggal = this.timeDiffCalc(new Date(res[0].tanggal_akad),new Date());
                this.$forceUpdate();
            });
        },
         format(tgl) {
            return this.$datefns.format(
                new Date(tgl),
                "dddd, DD MMMM YYYY",
                 {
                  locale: id,
                }
            )
        },
         timeDiffCalc(dateFuture, dateNow) {
            let diffInMilliSeconds = Math.abs(dateFuture - dateNow) / 1000;

            // calculate days
            const days = Math.floor(diffInMilliSeconds / 86400);
            diffInMilliSeconds -= days * 86400;

            // calculate hours
            const hours = Math.floor(diffInMilliSeconds / 3600) % 24;
            diffInMilliSeconds -= hours * 3600;

            // calculate minutes
            const minutes = Math.floor(diffInMilliSeconds / 60) % 60;
            diffInMilliSeconds -= minutes * 60;

            let difference = '';
            if (days > 0) {
            difference += (days === 1) ? `${days}, ` : `${days}, `;
            }

            difference += (hours === 0 || hours === 1) ? `${hours}, ` : `${hours}, `;

            difference += (minutes === 0 || hours === 1) ? `${minutes}` : `${minutes}`; 

            return difference;
        },
    },
}
</script>
